import React from 'react';
import { HashRouter, Route, BrowserRouter as Router, Switch, Redirect, Link } from "react-router-dom";

import SignIn from './pages/login/login.js';
import DoctorPage from './pages/doctor/doctor.js';
import HospitalPage from './pages/hospital/hospital.js';

import './App.css';

//import { ApolloProvider } from '@apollo/react-hooks';

import { MockedProvider } from '@apollo/react-testing';

import QueriesMock from './graphql/mocks/queries_mock.js';

const routing = (

  <Router basename={process.env.PUBLIC_URL}>
    <div>
      <Switch>
        <Route path="/" exact component={SignIn}></Route>
        <Route path="/DoctorPage" component={DoctorPage}></Route>
        <Route path="/HospitalPage" component={HospitalPage}></Route>
        <Redirect from="*" to="/" />
      </Switch>
    </div>
  </Router>

);


/* Use this to fetch directly to the API */
// function App({ client }) {
//   return (
//     <ApolloProvider client={client}>
//       <div className="App">
//         {routing}
//       </div>
//     </ApolloProvider>
//   );
// }


/* Use this to use the mocked data instead of fetching the API */
function App({ client }) {
  return (
    <MockedProvider mocks={QueriesMock} addTypename={false}>
      <div className="App">
        {routing}
      </div>
    </MockedProvider>
  );
}

export default App;
